import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Users, BookOpen, Globe, Sparkles, Award, FileText, ChevronDown, ChevronUp } from 'lucide-react';

const RecruitmentPage = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    resume: '',
    coverLetter: '',
    motivationVisible: false,
  });

  const handleChange = (e) => {
    if (e.target.name === 'resume') {
      setFormData({
        ...formData,
        [e.target.name]: e.target.files[0],
      });
    } else {
      setFormData({
        ...formData,
        [e.target.name]: e.target.value,
      });
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // 处理表单提交，例如将数据发送到服务器
    alert('申請已提交！');
  };

  const toggleMotivation = () => {
    setFormData((prevState) => ({
      ...prevState,
      motivationVisible: !prevState.motivationVisible,
    }));
  };

  return (
    <div className="bg-gradient-to-b from-purple-100 to-white min-h-screen py-12">
      <div className="container mx-auto px-4">
        <motion.h1 
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-4xl font-bold text-center text-purple-800 mb-12"
        >
          加入我們
        </motion.h1>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <motion.div
            initial={{ opacity: 0, x: -50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="bg-white rounded-lg shadow-xl p-6"
          >
            <h2 className="text-2xl font-semibold text-purple-700 mb-4">我們在尋找</h2>
            <ul className="space-y-4">
              <li className="flex items-start">
                <Users className="text-purple-600 mr-2 mt-1 flex-shrink-0" />
                <span>對人權和性別平等議題有熱情的學生</span>
              </li>
              <li className="flex items-start">
                <BookOpen className="text-purple-600 mr-2 mt-1 flex-shrink-0" />
                <span>具備研究能力和批判性思維的人才</span>
              </li>
              <li className="flex items-start">
                <Globe className="text-purple-600 mr-2 mt-1 flex-shrink-0" />
                <span>願意為校園和社會帶來正面改變的行動者</span>
              </li>
              <li className="flex items-start">
                <Sparkles className="text-purple-600 mr-2 mt-1 flex-shrink-0" />
                <span>具有創新思維，能夠提出新穎解決方案的創意人才</span>
              </li>
            </ul>
          </motion.div>
          
          <motion.div
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.4 }}
            className="bg-white rounded-lg shadow-xl p-6"
          >
            <h2 className="text-2xl font-semibold text-purple-700 mb-4">我們提供</h2>
            <ul className="space-y-4">
              <li className="flex items-start">
                <BookOpen className="text-purple-600 mr-2 mt-1 flex-shrink-0" />
                <span>深入學習人權和性別平等知識的機會</span>
              </li>
              <li className="flex items-start">
                <Users className="text-purple-600 mr-2 mt-1 flex-shrink-0" />
                <span>參與重要研究項目和政策制定的經驗</span>
              </li>
              <li className="flex items-start">
                <Globe className="text-purple-600 mr-2 mt-1 flex-shrink-0" />
                <span>與各界專家學者交流的平台</span>
              </li>
              <li className="flex items-start">
                <Award className="text-purple-600 mr-2 mt-1 flex-shrink-0" />
                <span>在學術和實踐中取得成就的機會</span>
              </li>
            </ul>
          </motion.div>
        </div>

        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.6 }}
          className="bg-white rounded-lg shadow-xl p-6 mt-8"
        >
          <h2 className="text-2xl font-semibold text-purple-700 mb-4 text-center">申請流程</h2>
          <p className="text-gray-700 text-center mb-4">
            有意加入我們的人才，請按照以下步驟申請：
          </p>
          <ol className="list-decimal list-inside space-y-4 text-center">
            <li>填寫在線申請表</li>
            <li>提交個人履歷和動機信</li>
            <li>通過初步篩選後將收到面試邀請</li>
            <li>完成面試並等待結果通知</li>
          </ol>
        </motion.div>

        {/* 申請表部分 */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.8 }}
          className="bg-white rounded-lg shadow-xl p-6 mt-8"
        >
          <h2 className="text-2xl font-semibold text-purple-700 mb-4 text-center">填寫申請表</h2>
          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="flex flex-col">
              <label className="text-purple-700 font-semibold mb-2" htmlFor="name">姓名</label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleChange}
                className="p-3 border border-purple-300 rounded-lg"
                required
              />
            </div>
            <div className="flex flex-col">
              <label className="text-purple-700 font-semibold mb-2" htmlFor="email">Email</label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                className="p-3 border border-purple-300 rounded-lg"
                required
              />
            </div>
            <div className="flex flex-col">
              <label className="text-purple-700 font-semibold mb-2" htmlFor="resume">履歷</label>
              <input
                type="file"
                name="resume"
                accept=".pdf,.doc,.docx"
                onChange={handleChange}
                className="p-3 border border-purple-300 rounded-lg"
                required
              />
            </div>

            {/* 動機信部分，初始隱藏 */}
            <div className="flex flex-col">
              <button
                type="button"
                onClick={toggleMotivation}
                className="flex items-center justify-between px-4 py-2 bg-purple-600 text-white rounded-lg font-semibold hover:bg-purple-700 transition duration-300"
              >
                動機信
                {formData.motivationVisible ? <ChevronUp /> : <ChevronDown />}
              </button>
              <AnimatePresence>
                {formData.motivationVisible && (
                  <motion.div
                    initial={{ opacity: 0, height: 0 }}
                    animate={{ opacity: 1, height: "auto" }}
                    exit={{ opacity: 0, height: 0 }}
                    transition={{ duration: 0.3 }}
                    className="mt-4"
                  >
                    <div className="flex flex-col">
                      <label className="text-purple-700 font-semibold mb-2" htmlFor="motivation1">1. 為什麼您認為自己適合這個崗位？請具體說明您過去的相關經驗。</label>
                      <textarea
                        name="motivation1"
                        value={formData.motivation1}
                        onChange={handleChange}
                        className="p-3 border border-purple-300 rounded-lg"
                        rows="4"
                        required
                      ></textarea>
                    </div>
                    <div className="flex flex-col mt-4">
                      <label className="text-purple-700 font-semibold mb-2" htmlFor="motivation2">2. 您對於性別平等或人權領域有什麼具體的興趣或目標？</label>
                      <textarea
                        name="motivation2"
                        value={formData.motivation2}
                        onChange={handleChange}
                        className="p-3 border border-purple-300 rounded-lg"
                        rows="4"
                        required
                      ></textarea>
                    </div>
                    <div className="flex flex-col mt-4">
                      <label className="text-purple-700 font-semibold mb-2" htmlFor="motivation3">3. 請舉例說明您曾經參與的有關社會公益或志願者活動，以及您從中學到的經驗。</label>
                      <textarea
                        name="motivation3"
                        value={formData.motivation3}
                        onChange={handleChange}
                        className="p-3 border border-purple-300 rounded-lg"
                        rows="4"
                        required
                      ></textarea>
                    </div>
                  </motion.div>
                )}
              </AnimatePresence>
            </div>

            <div className="text-center mt-8">
              <button
                type="submit"
                className="px-6 py-3 bg-purple-600 text-white rounded-full font-semibold hover:bg-purple-700 transition duration-300"
              >
                提交申請
              </button>
            </div>
          </form>
        </motion.div>
      </div>
    </div>
  );
};

export default RecruitmentPage;
